 <template>
  
    <div class="middle-index">
      <img src="../../static/img/indexBG.jpg" alt="">
      <div class="tab">
          <img src="../../static/img/back.png" alt="" @click="close()">
          <!-- <img src="../../static/img/close.png" alt="" @click="close()"> -->
          <span></span>
      </div>
      <div class="content">
         <img src="../../static/img/rule.png" alt="">
         <div class="main_content" style="padding:0 0.55rem">
             <div class="caizuan">
                <p></p>
                <p><br />61100100<br /></p>
            </div>
            <div class="caizuan">
                <p></p>
                <p>510101*3636</p>
            </div>
            <div class="caizuan">
                <p></p>
                <p>1</p>
            </div>
         </div>
      </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
           
        }
    },
    methods: {
      close:function(){
          this.$router.push('/index');
      }
    }
}
</script>

<style scoped>
ul li{
    list-style: none;
}
  .clearfix:after {
			display: block;
			content: '';
			clear: both;
			width: 0px;
			height: 0px;
 }

.middle-index>img{
    height: 100%;
    width: 100%;
    position: fixed;
}
.tab{
   background: #ffffff;
    height: 1.11rem;
    position: relative;
    width: 100%;
    line-height: 1.11rem;
}
.tab img{
        width: 0.66rem;
        margin-left: 0.25rem;
   		margin-top: 0.22rem;
}
.tab span{
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    font-size: 0.44rem;
}
.content{
    height: 15rem;
    width: 100%;
    position: relative;
    text-align: center;
}
.content>img{
        height: 1.38rem;
        padding-top: 0.1rem;
}
.caizuan{
    background: #ffffff;
    border-radius: 0.3rem;
    padding: 0 0.55rem;
    padding-top: 0.69rem;
    padding-bottom: 0.69rem;
    margin-bottom: 0.55rem;
}
.caizuan p:first-of-type{
    width:4.16rem;
    height:0.66rem;
    background: #1F60B7;
    font-size: 0.38rem;
    color: #ffffff;
    margin: 0 auto;
    border-radius: 0.3rem;
    margin-bottom: 0.55rem;
    line-height: 0.66rem;
}
.caizuan p:last-of-type{
    font-size: 0.38rem;
    text-align: left;
}

</style>





