import Vue from 'vue'

import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
 import Login from '@/components/Login'
import Resigter from '@/components/Resigter'
import Forget_password from '@/components/Forget_password'
import Index from '@/components/Index'
import Help from '@/components/Help'
import Caizuan from '@/components/Caizuan'
import Energy from '@/components/Energy'
import My_account from '@/components/My_account'
import Logout from '@/components/Logout'
import Accelerate from '@/components/Accelerate'
import Rule from '@/components/Rule'
import Goods from '@/components/Goods'
import Herald from '@/components/Herald'
import Goods_store from '@/components/Goods_store'
import Achieve from '@/components/Achieve'
import Rank from '@/components/Rank'
import Begin from '@/components/Begin'
import Challage from '@/components/Challage'
import ChallageFailure from '@/components/ChallageFailure'
import ChallageSuccess from '@/components/ChallageSuccess'
import InviteFirend from '@/components/InviteFirend'
import GuessCaizuan from '@/components/GuessCaizuan'
import PaySuccess from "@/components/PaySuccess"
import PayFailure from "@/components/PayFailure"
import IndexOld from "@/components/IndexOld"
import MyGuess from "@/components/MyGuess"

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component:Login,
      meta: {
          title: ""
      }
  },
  {
      path: '/',
      name: 'Login',
      component:Login,
      meta: {
          title: ""
      }

  },
  {
    path: '/register',
    name: 'register',
    component:Resigter,
    meta: {
        title: ""
    },
},
{
    path: '/forget_password',
    name: 'forget_password',
    component:Forget_password,
    meta: {
        title: ""
}

},
{
    path: '/index',
    name: 'index',
    component:Index,
    meta: {
        title: ""
}

},
{
    path: '/help',
    name: 'help',
    component:Help,
    meta: {
        title: ""
}

},
{
    path: '/caizuan',
    name: 'caizuan',
    component:Caizuan,
    meta: {
        title: ""
}

},
{
    path: '/energy',
    name: 'energy',
    component:Energy,
    meta: {
        title: ""
}

},
{
    path: '/my_account',
    name: '',
    component:My_account,
    meta: {
        title: ""
}

},
{
    path: '/logout',
    name: '',
    component:Logout,
    meta: {
        title: ""
}

},
{
    path: '/accelerate',
    name: '',
    component:Accelerate,
    meta: {
        title: ""
}

},
{
    path: '/rule',
    name: '',
    component:Rule,
    meta: {
        title: ""
}

},
{
    path: '/goods',
    name: '',
    component:Goods,
    meta: {
        title: ""
}

},
{
    path: '/herald',
    name: '',
    component:Herald,
    meta: {
        title: ""
}

},
{
    path: '/good_store',
    name: '',
    component:Goods_store,
    meta: {
        title: ""
}

},
{
    path: '/achieve',
    name: '',
    component:Achieve,
    meta: {
        title: ""
}

},
{
    path: '/rank',
    name: '',
    component:Rank,
    meta: {
        title: ""
}

},
{
    path: '/begin',
    name: '',
    component:Begin,
    meta: {
        title: ""
}

},
{
    path: '/Challage',
    name: '',
    component:Challage,
    meta: {
        title: ""
}

},
{
    path: '/ChallageFailure',
    name: '',
    component:ChallageFailure,
    meta: {
        title: ""
}

},
{
    path: '/ChallageSuccess',
    name: '',
    component:ChallageSuccess,
    meta: {
        title: ""
}

},
{
    path: '/InviteFirend',
    name: '',
    component:InviteFirend,
    meta: {
        title: ""
}

},
{
    path: '/guessCaizuan',
    name: '',
    component:GuessCaizuan,
    meta: {
        title: ""
}

},
{
    path: '/PaySuccess',
    name: '',
    component:PaySuccess,
    meta: {
        title: ""
}

},
{
    path: '/PayFailure',
    name: '',
    component:PayFailure,
    meta: {
        title: ""
}

},
{
    path: '/IndexOld',
    name: 'indexold',
    component:IndexOld,
    meta: {
        title: ""
}

},
/*{
    path: '/Index',
    name: 'index',
    component:Index,
    meta: {
        title: ""
}

}*/,
{
    path: '/MyGuess',
    name: 'MyGuess',
    component:MyGuess,
    meta: {
        title: ""
}

}
  ]
})



