            <input type="text" class="form-control" v-model="mobile" placeholder="" required=""  style="padding:0 0.23rem">
            <span style="color:red;width:32%">{{mobilemessage}}</span>
        </div>
        <div class="form-group">
            <input type="password" class="form-control" v-model="code" placeholder="" required="" style="padding:0 0.23rem">
            <span @click="requirecode()" v-if="type==1"></span>
            <span  v-else>{{codemessage}}</span>
        </div>  
        <div class="form-group">
            <input type="password" class="form-control" v-model="password" placeholder="" required="" style="padding:0 0.23rem;width:32%">
            <span style="color:red;width:28%">{{pwdmessage}}</span>
        </div> 
        <div class="xieyi" @click="kai()">
            <input type="checkbox" style="background: transparent;vertical-align: middle;" checked="true">
        </div> 
        <div class="login">
            <p @click="end()"></p>
        </div>
    </div>
    
    
    <div class="box" style="display: none;">
    <div class="protocol" >
    	<div  class="top">
            <img @click="guan()"  style="float: right;margin-right: 0.25rem;" src="/static/img/close.18ce7f6.png" alt="" >
       </div>
