<script>
export default {
    data () {
        return {
            selectItem:0,
            sex:[{"sex":""},{"sex":""}],
            mobile: '',
            password: '',
            code:'',
            name:'',
            mobilemessage:"",
            pwdmessage:"",
            type:"1",
            codemessage:60
        }
    },
    methods: {
    	guan:function(){
    		$(".box").fadeOut(1000)
    	},
    	kai:function(){
    		$(".box").fadeIn(1000)
    	},
       back:function(){
           window.history.go(-1);
       },
       toColor:function(index){
            this.selectItem=index;
       },
       end:function(){
        
              var that = this;
            var reg=/^[1][3578][0-9]{9}$/;
            if(that.mobile==""){
                that.mobilemessage="";
            }else{
                  that.mobilemessage="";
                  that.pwdmessage="";
               if(reg.test(that.mobile)&&that.password!==""){
                  
                   that.$Post('/resets' ,{mobile: that.mobile,code:that.code, pwd:that.password,type:4}, function(data){
                       console.log(data);
                     
                       if(data.code=="221"){
//                         alert("");
							mizhu.alert('', '');
                       }else{
                           that.$router.push('/login'); 
                       }
                       
                    }); 
               }else if(that.password==""){
                     that.pwdmessage="";
               }else{
                    that.mobilemessage="";
               }
            }
       },
        requirecode:function(){
            var that = this;
           
            if(that.mobile!==""){
               var reg=/^[1][3578][0-9]{9}$/;
               if(reg.test(that.mobile)){
                  that.type=2;
                  that.mobilemessage="";
                    setInterval(()=>{
                    that.codemessage--;
                        if(that.codemessage==0){
                            that.type=1;
                        }
                    },1000);
                    that.codemessage=60;
                    that.$Post('/smscode' ,{mobile: that.mobile,type:"4"}, function(data){
                        console.log(data);
                          if(data.code=="203"){
                           
//                         alert("");
mizhu.alert('', '');
                         
                       }
                        });
               }else{
                   that.mobilemessage="";
               }
                 
            }else if(that.mobile==""){
                that.mobilemessage="";
            }
            
       }
    }
}
</script>

<style scoped>
