    font-size: 0.44rem;
}
.active{
  color:#ffffff;
}
  .clearfix:after {
			display: block;
			content: '';
			clear: both;
			width: 0px;
			height: 0px;
 }
.middle-box{
    height: 17.777rem;
    width: 100%;
    position: fixed;
    background: url(../../static/img/indexBG.jpg) no-repeat;
    background-size:100% 100%; 
}
.title{
    padding-top:2.22rem;
}
.title h2{
   text-align: center;
    font-size: 0.5rem;
    margin: 0 auto;
    color:#ffffff;
}
.form-group{
    width:8.33rem;
