    border:none;
    outline: none;
}

.login{
    padding-top:1.61rem;
}
.login p{
    width:5.38rem;
    height:1.25rem;
    margin: 0 auto;
    text-align: center;
    font-size: 0.44rem;
    color:#ffffff;
    border:1px solid #ffffff;
    line-height: 1.25rem;
    border-radius: 0.3rem;
}
.xieyi{
    font-size:0.33rem;
