<template>
 <div class="middle-box text-center">
    <div  class="tab">
        <img src="/static/img/back.9fbd588.png" alt="" @click="back()">
        <!-- <img  src="/static/img/close.18ce7f6.png" alt="">  -->
        <span ></span>
    </div>
    <div style="width:8.33rem;margin:0 auto;padding-top:3.08rem;display: none;" class="reg" >
    
        <div class="form-group">
            <input type="text" class="form-control" v-model="mobile" placeholder="" required="" style="padding:0 0.23rem">
            <span style="color:red;font-size:0.38rem">{{mobilemessage}}</span>
        </div>
        <div class="form-group">
            <input type="password" class="form-control" v-model="code" placeholder="" required="" style="padding:0 0.23rem;width:60%">
            <span @click="requirecode()" v-if="type==1"></span>
            <span  v-else>{{codemessage}}</span>
        </div>  
        <div class="form-group">
            <input type="password" class="form-control" v-model="password" placeholder="" required="" style="padding:0 0.23rem">
        </div> 
         <div class="form-group">
            <input type="yanzhengma" class="form-control" v-model="yanzhengma" placeholder="" required="" style="padding:0 0.23rem">
        </div> 
        <div class="xieyi" @click="kai()">
            <input type="checkbox" style="background: transparent;vertical-align: middle;" checked="true">
        </div> 
        <div class="login">
            <p @click="next()"></p>
        </div>
    </div>
    <div class="name" style="width:8.33rem;margin:0 auto;padding-top:3.08rem;display: block;">

        <div class="form-group">
            <input type="text" class="form-control"    v-model="name" placeholder="" required="" ref="count" style="padding:0 0.23rem">
        </div>
        <div class="note">
            <p></p>
        </div>
        <div class="sex">
            <span></span>
            <ul>
                <li v-for="(tmp,index) in sex" :key="index" @click="toColor(index,tmp.id)" v-bind:class="{active:index==selectItem}">{{tmp.sex}}</li>
            </ul>
        </div>
         <div class="login" style="padding-top:2.73rem">
            <p @click="end()"></p>
        </div>
    </div>
    
    <div class="box" style="display: none;">
    <div class="protocol" >
    	<div  class="top">
            <img @click="guan()"  style="float: right;margin-right: 0.25rem;" src="/static/img/close.18ce7f6.png" alt="" >
       </div>
    	<p> Iontrum<br />
    		 Iontrum Iontrum (  ) Iontrum Iontrum 

