	<div  class="agree" >
            <span></span>
      </div>
    </div>
    </div>
</div>
</template>
<script>

	
export default {
    data () {
        return {
            selectItem:0,
            sex:[{"sex":"",id:"2"},{"sex":"",id:"1"}],
            mobile: '',
            password: '',
            code:'',
            name:'',
            yanzhengma:"",
            codemessage:60,
            type:1,
            mobilemessage:"",
            mysex:"",
//          invite:""
        }
    },
    watch:{   //watch()change
                name:{
                    handler:function(){
                        var _this = this;
                        
                        var _sum = 20; //100
                       
                        _this.$refs.count.setAttribute("maxlength",_sum);
                        var reg = /[\u4e00-\u9fa5]{1}/g;            //
                        var notReg = /\w{1}/g;                      //
	                    var resultCn = _this.$refs.count.value.match(reg);
	                    var resultEn = _this.$refs.count.value.match(notReg);
	                    
	                if(resultCn){
                       _sum= _sum- (resultCn.length*2);
                    }
                    if(resultEn){

                        _sum= _sum- resultEn.length;
                    }
                    
                    if(_sum<=0){

                 	    var finalLen = _this.$refs.count.value.length+_sum;
                        var value =  _this.$refs.count.value.substring(0,finalLen);
                        _this.$refs.count.setAttribute("maxlength",_sum);
//                  
                        _this.name =value

                    }
                       
                    },
                    deep:true
                }
            },
    created:function(){
    	var that = this;
    	var userInfo= window.sessionStorage.getItem('userInfo'); 
   	    that.userInfo = JSON.parse(userInfo);
			if(that.userInfo){
				    
					that.userInfo=JSON.parse(userInfo);
					that.invite_code = that.userInfo.invite_code;
					that.user_id = that.userInfo.user_id
//					alert(that.invite_code)
			}else{
					that.invite_code = "";	
					that.user_id =""
				}
//		alert(window.location.href.split("?")[1]);
		that.yanzhengma = window.location.href.split("?")[1]
    },
    methods: {
    	guan:function(){
    		$(".box").fadeOut(1000)
    	},
    	kai:function(){
    		$(".box").fadeIn(1000)
    	},
       back:function(){
            window.history.go(-1);
       },
       next:function(){
       		var that = this;
//          var reg=/^[1][3578][0-9]{9}$/;
 			var reg=/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
            if(that.mobile==""){
//              alert("");
   				mizhu.alert('', '');
                return false;
            }else if(that.password==""){
//                alert("");
                     mizhu.alert('', '');
                  return false;
               }
            $(".name").css("display","block");
            $(".reg").css("display","none");
       },
       toColor:function(index,id){
            this.selectItem=index;
            this.mysex=id;    
       },
       end:function(){
            var that = this;
            if(that.name == ""){
//          	alert("");
				mizhu.alert('', '');
            	return false;
            }
            that.$Post('/signup' ,{mobile: that.mobile,code:that.code,pwd:that.password,user_name:that.name,sex:that.mysex,invite_code:that.yanzhengma}, function(data){
                        console.log(data);
                        if(data.code=="207"){
                            that.$router.push('/login');
                        }else if(data.code=="213"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="210"){
                        	alert("");
//                      	mizhu.alert('', '');
                        }else if(data.code=="209"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="204"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="214"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="216"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="217"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="218"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="220"){
//                      	alert("");
                        	mizhu.alert('', '');
                        }else if(data.code=="215"){
//                          alert("");
                            mizhu.alert('', '');
                        }
                         
            });
       },
       requirecode:function(){
            var that = this;
           
            if(that.mobile!==""){
               var reg=/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/;
               if(reg.test(that.mobile)){
                  that.type=2;
                  that.mobilemessage="";
                    setInterval(()=>{
                    that.codemessage--;
                        if(that.codemessage==0){
                            that.type=1;
                        }
                    },1000);
                    that.codemessage=60;
                    that.$Post('/smscode' ,{mobile: that.mobile,type:"2"}, function(data){
                        console.log(data);
                        if(data.code=="203"){                           
//                         alert("");                       
							 mizhu.alert('', '');
                       }
                        });
               }else{
                   that.mobilemessage="";
                   
               }
                 
            }else if(that.mobile==""){
                that.mobilemessage="";
            }
            
       }
    }
}
</script>

<style scoped>
input{
	color: #fff;
}
.protocol{
	    width: 8.88rem;
    height: 100%;
    background: #fff;
    overflow-y: scroll;
    overflow-x: hidden;
    border-radius: 0.27rem;
    margin: 0 auto;
	
}
.protocol>h3{
	font-size: 0.22rem;
	color: #101010;
}
.protocol>p{
	margin-top:1.27rem;
	font-size: 0.22rem;
	color: #101010;
	padding:0rem 0.27rem 0;
	padding-bottom: 1.2rem;
}
.box{
	width: 8.88rem;
    height: 80%;
    margin: 0 auto 0;
    position: relative;
    /*top: -385px;*/
    left:50%;
	top:50%;
	position: fixed;
	-webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    transform:translate(-50%,-50%);
}
.agree{
	font-size: 0.33rem;
	height: 1.11rem;
	line-height: 1.11rem;
	padding-left:0.69rem;
	border-top:1px solid #E5E5E5;
	position: absolute;
    bottom: 0;
    border-radius: 0 0 10px 10px;
	width:8.19rem;
	background: #fff;	    
}
.top{
	width:100%;
	height:1.1rem;
	line-height:1.1rem;
	position:absolute;
	top:0rem;
	right:0rem;
	background: #fff;
	border-radius: 10px 10px 0 0;
	
}
.top>img{
	width: 0.66rem;
	margin-left: 0.25rem;
    	margin-top: 0.22rem;
}
.tab{
   background: #ffffff;
    height: 1.11rem;
    position: relative;
    width: 100%;
    line-height: 1.11rem;
}
.tab img{
        width: 0.66rem;
        margin-left: 0.25rem;
    	margin-top: 0.22rem;
}
.tab span{
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    font-size: 0.44rem;
}
.active{
  color:#ffffff;
}
  .clearfix:after {
			display: block;
			content: '';
			clear: both;
			width: 0px;
			height: 0px;
 }
.middle-box{
    height: 100%;
    width: 100%;
    position: fixed;
    background: url(../../static/img/indexBG.jpg) no-repeat;
    background-size:100% 100%; 
}
.title{
    padding-top:2.22rem;
}
.title h2{
   text-align: center;
    font-size: 0.5rem;
    margin: 0 auto;
    color:#ffffff;
}
.form-group{
    width:8.33rem;
    margin:0 auto;
    border-bottom:1px solid #BBBBBB;
}
.form-group input{
    height:1rem;
    margin:0 auto;
    border: none;
    display:inline-block;
    background: transparent;
    font-size: .4rem;
}
.form-group span{
    font-size: 0.38rem;
    color:#ffffff;
    padding:0.23rem;
    line-height: 1rem;
    float: right;
}
.form-group input::-webkit-input-placeholder{
         font-size: 0.38rem;  
        color: #BBBBBB;
}
.form-group input:focus{
    border:none;
    outline: none;
}

.login{
    padding-top:1.61rem;
}
.login p{
    width:5.38rem;
    height:1.25rem;
    margin: 0 auto;
    text-align: center;
    font-size: 0.44rem;
    color:#ffffff;
    border:1px solid #ffffff;
    line-height: 1.25rem;
    border-radius: 0.3rem;
}
.xieyi{
    font-size:0.33rem;
    color:#ffffff;
    padding-top:0.41rem;
}
.name{
    display: none;
}
.note p{
    color:#ffffff;
    width:7.5rem;
    text-align: left;
    font-size: 0.33rem;
    margin:0 auto;
    padding-top:0.36rem;
}
.sex{
    font-size:0.38rem;
     color:#ffffff;
     width:7.5rem;
    margin:0 auto;
    padding-top:0.36rem;
}
ul{
    float:right;
}
ul li{
    list-style: none;
    float:right;
    color:#737373;
    width: 2.2rem;
    text-align: right;
}
</style>





